.container {
  // 
  max-width: 1100px;
  //  100% max-width 
  width: 100%;
  padding-left: $padding-x;
  padding-right: $padding-x;
  margin: 0 auto;
  @include media-breakpoint-up(md) {
    padding-left: $padding-x*2;
    padding-right: $padding-x*2;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  //  col 
  margin-left: -$padding-x;
  margin-right: -$padding-x;
  @include media-breakpoint-up(md) {
    padding-left: -$padding-x*2;
    padding-right: -$padding-x*2;
  }
  >* {
    width: 100%;
    max-width: 100%;
    padding-left: $padding-x;
    padding-right: $padding-x;
  }
}

@each $name, $value in $cols {
  .col-#{$name} {
    width: $value;
    padding-left: $padding-x;
    padding-right: $padding-x;
    position: relative;
  }
}